/*--------FONTS--------*/
body {
    font-family: 'Roboto', sans-serif;
    background: #eeeded;
}

h1, h2, h3, h4, label {
    font-family: 'Lobster', cursive;
}

h4 {
    font-size: 1.7rem;
}

i {    

    margin: 0 10px;
}

.size-iconcs {
    display: block;    
    text-align: center;
    font-size: 50px;
}

.serch-icons {
    font-size: 30px;
}

a {
    color: black;
}


.alert-danger {
    background: rgb(182, 9, 9);
    color: white;
}



/*--------HEADER--------*/
.bg-space-img {
    background-image: url(./imgs/space-bg.jpg);
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 20rem;
    width: 100%;
    position: relative;
    margin-bottom: 15rem;
}

.card-home {
    width: 40rem;
    background: #eeeded;
    color: black;
    padding: 2rem;
    position: absolute; 
    width: 50%;
    margin:0 auto ;
    margin-left: 50%;
    transform: translateX(-50%);
    margin-top: 14rem;
    border: 1px solid rgba(0,0,0,.10);
    box-shadow: 1px 4px 3px 2px rgba(0, 0, 0, 0.20);    
}

@media (max-width: 796px) {
    .card-home {
        
        background-color: white;
        color: black;
        padding: 2rem;
        position: absolute; 
        width: 100%;
        
        margin-top: 14rem;
        border: 1px solid rgba(0,0,0,.10);
    }
}


.title-space::after {
    content: '';
    width: 80%;
    height: 1px;
    display: block;
    background-color: rgba(0, 0, 0, 0.30);
    margin: 0 auto;
}











/*--------PRINCIPAL PAGE--------*/
.img-astronaunt {
    background-image: url(./imgs/astronaut.png);
    background-position: center center;
    background-size: contain;
    background-repeat: no-repeat;
    height: 12rem;
}

.facts-space {
    background-image: url(./imgs/moon-bg.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 20px;
    
}
.bg-titles-facts {
    background-color: rgba(0, 0, 0, 0.60);
    color: white;
    padding: 2rem 0;
}


@media (max-width: 796px) {

    .facts-space {
        background-color: rgba(0,0,0,.75);
        padding: 20px;
        color: white;
        font-size: .7rem;
        line-height: .9rem;
        padding: 1.5rem 0;
    }

    .facts-space h3 {
        font-size: 1.4rem;
    }
    
}




.iss-bg{
    background-image: url(./imgs/iss.jpeg);
    background-position: center center;
    background-size: cover;    
    background-repeat: no-repeat;
    height: 15rem;
    width: 100%;
}


.sectionButton {    
    padding: 5rem 0;
    background-color: black;
    color: white;
}



.searchNasaGalery {
    border: 1px solid rgba(0,0,0,.10);
    padding: 1rem;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.10);
    transition: all ease .8s;
    margin: 1px;
}

.searchNasaGalery:hover {
    box-shadow: 1px 4px 3px 2px rgba(0, 0, 0, 0.15);
}

.linkedit {
    color: black;  
    font-size: 1rem;  
    transition: all ease .2s;
}

.linkedit:hover {
    text-decoration: none;
    color: black;
    font-weight: 500;

}


@media (max-width: 370px) {
    .linkedit {        
        font-size: 0.85rem;  
    }
}





/*--------IMAGENES NASA--------*/

.img-Nasa {
    width: 100%;
    height: 15rem;
    object-fit: cover;
}


/*--------NAV--------*/

nav .navbar-toggler {
    font-weight: bold;
    
}


/*--------SPINNER--------*/
.spinner {
    width: 80px;
    height: 80px;
    background-color: rgb(59, 58, 58);
  
    margin: 100px auto;
    -webkit-animation: sk-rotateplane 1.2s infinite ease-in-out;
    animation: sk-rotateplane 1.2s infinite ease-in-out;
  }
  
  @-webkit-keyframes sk-rotateplane {
    0% { -webkit-transform: perspective(120px) }
    50% { -webkit-transform: perspective(120px) rotateY(180deg) }
    100% { -webkit-transform: perspective(120px) rotateY(180deg)  rotateX(180deg) }
  }
  
  @keyframes sk-rotateplane {
    0% { 
      transform: perspective(120px) rotateX(0deg) rotateY(0deg);
      -webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg) 
    } 50% { 
      transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
      -webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg) 
    } 100% { 
      transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
      -webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
    }
  }